import styles from '../../Pages/Layout/layout.module.css'
import { Outlet, Link, NavLink } from "react-router-dom";
import React from 'react';

const Layout =()=>{
    return(
        <div>
        <i className={`bi bi-list ${styles.mobileNavToggle} d-xl-none`}></i>
        <header id={styles.header} className="d-flex flex-column justify-content-center">
            <nav id="navbar" className={`navbar ${styles.navMenu}`}>
                <ul>
                    <li><NavLink to="/" > <i className="bx bx-home"></i><span>Home</span></NavLink></li>
                    <li><NavLink to="/about" > <i className="bx bx-user"></i><span>About</span></NavLink></li>
                    <li><NavLink to="/projects" > <i className="bx bx-book-content"></i><span>Portfolio</span></NavLink></li>
                    <li><NavLink to="/contact" > <i className="bx bx-envelope"></i><span>Contact</span></NavLink></li>
                </ul>
            </nav>
        </header>
        <Outlet />
        </div>
    )
};
export default Layout;


