import styles from '../../Pages/About/about.module.css';
import MyPic from '../../assets/img/MyPic.jpg';
import React from 'react';

const About =()=>{
    return(
        <section id={styles.about} className={styles.about}>
<div className="container" data-aos="fade-up">
    <div className={styles.sectionTitle}>
        <h2>About Me</h2>
        <p> I am a dedicated and results-driven Customer Success Executive with a passion for building strong and lasting relationships. With a background in understanding and exceeding customer needs, I thrive in ensuring exceptional customer experiences. My journey in the world of customer success has equipped me with the skills to actively listen, empathize, and provide solutions that not only meet but exceed expectations.
        </p>
    </div>

    <div className="row">
        <div className="col-lg-4">
            <img src={MyPic} className="img-fluid" alt="" />
        </div>
        <div className={`col-lg-8 pt-4 pt-lg-0 ${styles.content}`}>
            <h4 className={styles.aboutHeading}>Customer Sucesss Executive.</h4>
            <p className="fontItalic">Building and nurturing relationships is at the core of what I do. I am committed to the success and satisfaction of our clients and am excited about the opportunity to contribute to their growth and happiness. Let's work together to achieve great things.
            </p>
            <div className="row">
                <div className="col-lg-6">
                    <ul>
                        <li><i className="bi bi-chevron-right"></i> <strong>Phone:</strong> <span>+919487082115</span></li>
                        <li><i className="bi bi-chevron-right"></i> <strong>Email:</strong> <span>vaavamurali.anina@gmail.com</span></li>
                        <li><i className="bi bi-chevron-right"></i> <strong>Organization:</strong><span>Vaisesika</span></li>
                        <li><i className="bi bi-chevron-right"></i> <strong>Address:</strong> <span>Kochi, Kerala, INDIA</span></li>
                    </ul>
                </div>
                <div className="col-lg-6">
                    <ul>
                        <li><i className="bi bi-chevron-right"></i> <strong>Degree:</strong> <span> M.Sc,B.Ed </span></li>
                        <li><i className="bi bi-chevron-right"></i> <strong>College:</strong> <span>Pondicherry Univerity</span></li>
                        <li><i className="bi bi-chevron-right"></i> <strong>Birthday:</strong> <span>15 January, 1998</span></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

</div>
</section>
    )
}

export default About