import styles from '../../Pages/Projects/projects.module.css'
import React from 'react';
import book from '../../assets/img/book.png';
import chat from '../../assets/img/chat.png';
import Covid from '../../assets/img/Covid.png';
import fantastic from '../../assets/img/favIcon.png';
import hag from '../../assets/img/hag.png';
import website from '../../assets/img/website.png';
import ProgressBar from "@ramonak/react-progress-bar";

const Projects =()=>{
    return(
        <div>
        <section id={styles.skills} className={`section-bg ${styles.skills}`}>
        <div className="container" data-aos="fade-up">
            <div className={styles.sectionTitle}>
                <h2>Skills</h2>
                <br />
                <b>This is a professional skill set based on my work experience and acquired knowledge.</b>
            </div>
            <div className="row skills-content">
                <div className="col-lg-6">
                    <h3>Frontend</h3>
                    <div className={styles.progress}>
                        <span className={styles.skill}>ReactJS <i className="val">50%</i></span>
                        <ProgressBar completed = {50} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>Python<i className="val">60%</i></span>
                        <ProgressBar completed = {60} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>Javascript <i className="val">60%</i></span>
                        <ProgressBar completed = {60} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>HTML/CSS <i className="val">70%</i></span>
                        <ProgressBar completed = {70} bgColor = "blue" />
                    </div>

                </div>

                <div className="col-lg-6">
                    <h3>General</h3>
                    <div className={styles.progress}>
                        <span className={styles.skill}>French Speaking <i className="val">80%</i></span>
                        <ProgressBar completed = {80} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>French writing <i className="val">80%</i></span>
                        <ProgressBar completed = {80} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>TOSCA <i className="val">80%</i></span>
                        <ProgressBar completed = {80} bgColor = "blue" />
                    </div>
                    <div className={styles.progress}>
                        <span className={styles.skill}>API Testing<i className="val">70%</i></span>
                        <ProgressBar completed = {70} bgColor = "blue" />
                    </div>
                </div>
            </div>
        </div>
    </section>

    <section id={styles.portfolio} className={` section-bg ${styles.portfolio}`}>
        <div className="container" data-aos="fade-up">

            <div className={styles.sectionTitle}>
                <h2>Projects</h2>
                <b>This is a portfolio of my work, including both personal and production projects.</b>
            </div>

            <div className="row" data-aos="fade-up">
                <div className="col-lg-12 d-flex justify-content-center" data-aos="fade-up" data-aos-delay="100">
                    <ul id={styles.portfolioFlters}>
                        <li data-filter="*" className={styles.filterActive}>All</li>
                        <li data-filter=".filter-app">Apps</li>
                        <li data-filter=".filter-web">Websites</li>
                        <li data-filter=".filter-card">MERN</li>
                    </ul>
                </div>
            </div>

            <div className="row portfolio-container" data-aos="fade-up" data-aos-delay="200">
                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-app`}>
                    <div className={styles.portfolioWrap}>
                        <img src={book} className="img-fluid" alt="Book-IT ecommerce Store" />
                        <div className="portfolio-links">
                            <a href="https://akshatbookstore.netlify.app/" title="View App" target="_blank"><i
                                className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/Book-store-Reactjs" title="View github"
                                target="_blank"><i className="bx bxl-github"></i></a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>Book ecommerce React Firebase App</h5>
                            <i className="fa-brands fa-react" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-html5  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-js  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-stripe  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>


                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-web`}>
                    <div className={styles.portfolioWrap}>
                        <img src={website} className="img-fluid" alt="Harry Potter Website" />
                        <div className="portfolio-links">
                            <a href="https://akshatjalan.github.io/HarrypotterWebsite-akshat/" title="View website"
                                target="_blank"><i className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/HarrypotterWebsite-akshat" title="View github"
                                target="_blank"><i className="bx bxl-github"></i></a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>Harry Potter Website</h5>
                            <i className="fa-brands fa-html5  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-bootstrap fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-js  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>
                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-app`}>
                    <div className={styles.portfolioWrap}>
                        <img src={Covid} className="img-fluid" alt="Covid Status App" />
                        <div className="portfolio-links">
                            <a href="https://covidstatus-akshat.netlify.app/" title="View CovidApp"
                                target="_blank"><i className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/react-covid-status-app" title="View github"
                                target="_blank"><i className="bx bxl-github"></i></a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>Covid Status React App</h5>
                            <i className="fa-brands fa-react fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-solid fa-leaf  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>

                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-web`}>
                    <div className={styles.portfolioWrap}>
                        <img src={fantastic} className="img-fluid" alt="fantastic-beasts-website" />
                        <div className="portfolio-links">
                            <a href="https://akshatjalan.github.io/fantastic-beasts-website/"
                                title="View Reasearch Journal" target="_blank"><i className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/fantastic-beasts-website" title="View github"
                                target="_blank"><i className="bx bxl-github"></i></a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>Fantastic Beasts Website</h5>
                            <i className="fa-brands fa-html5  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-bootstrap  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>

                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-app`}>
                    <div className="portfolioWrap">
                        <img src={chat} className="img-fluid" alt="React Native chatApp" />
                        <div className="portfolio-links">
                            <a href="https://chatapp-react-native-e43d9.web.app/" title="View chatApp"
                                target="_blank"><i className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/react-native-chat-app" title="View github"
                                target="_blank"><i className="bx bxl-github"></i></a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>React Native Chat App</h5>
                            <i className="fa-brands fa-react  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-solid fa-mobile  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>

                <div className={`col-lg-4 col-md-6 ${styles.portfolioItem} filter-web`}>
                    <div className={styles.portfolioWrap}>
                        <img src={hag} className="img-fluid" alt="HaGGele-website" />
                        <div className="portfolio-links">
                            <a href="https://akshatjalan.github.io/haggele/" title="View HaGGele Website"
                                target="_blank"><i className="bx bx-link"></i></a>
                            <a href="https://github.com/Akshatjalan/haggele" title="View github" target="_blank"><i
                                className="bx bxl-github"></i> </a>
                        </div>
                        <br />
                        <div className={styles.portfolioTitle}>
                            <h5>HaGGele Website</h5>

                            <i className="fa-brands fa-html5  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-css3-alt  fa-lg" style={{ color: '#0563BB' }}></i>
                            <i className="fa-brands fa-bootstrap  fa-lg" style={{ color: '#0563BB' }}></i>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    </div>
    )
}
export default Projects